<template>
  <div class="list-box">
    <ul>
      <li v-for="item in msg">
        <div class="pic item-flex">
          <img v-bind:src="item.author.avatar_url">
        </div>
        <div class="box">
          <h2 v-text="item.title"></h2>
          <div class="detail">
            <div class="col"><i class="iconfont">&#xe605;</i> {{item.visit_count}}</div>
            <div class="col"><i class="iconfont">&#xe601;</i> {{item.reply_count}}</div>
            <div class="col"></div>
            <div class="col"><i class="iconfont">&#xe60a;</i> {{item.create_at | dateBeautiful}}</div>
          </div>
        </div>
      </li>
    </ul>
  </div>
  <div class="loading" @click="getMsgList()">加载更多...</div>
</template>

<script>
  import Vue from 'vue'
  import vueResource from 'vue-resource'
  Vue.use(vueResource);

  Vue.filter('dateBeautiful', function (value) {
    function goodTime(value){
       var now = new Date().getTime(),
       oldTime = new Date(value).getTime(),
       difference = now - oldTime,
       result='',
       minute = 1000 * 60,
       hour = minute * 60,
       day = hour * 24,
       halfamonth = day * 15,
       month = day * 30,
       year = month * 12,
       
       _year = difference/year,
       _month =difference/month,
       _week =difference/(7*day),
       _day =difference/day,
       _hour =difference/hour,
       _min =difference/minute;
       if(_year>=1) {result= ~~(_year) + " 年前"}
       else if(_month>=1) {result= ~~(_month) + " 个月前"}
       else if(_week>=1) {result= ~~(_week) + " 周前"}
       else if(_day>=1) {result= ~~(_day) +" 天前"}
       else if(_hour>=1) {result= ~~(_hour) +" 个小时前"}
       else if(_min>=1) {result= ~~(_min) +" 分钟前"}
       else result="刚刚";
       return result;
}
  return goodTime(value);
  })
  export default {
    ready: function() {
      this.$http.get('https://cnodejs.org/api/v1/topics?page='+this.page+'&tab=all&limit=10&mdrender=false').then(function(response) {
        var data = response.data;
        this.msg = this.msg.concat(data.data);
        this.page++;
      }, function(response) {
            // handle error
          });
    },
    data () {
      return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg: [],
      page: 1,
    }
  },
  methods: {
    getMsgList: function(){
      this.$http.get('https://cnodejs.org/api/v1/topics?page='+this.page+'&tab=all&limit=10&mdrender=false').then(function(response) {
        var data = response.data;
        this.msg = this.msg.concat(data.data);
        this.page++;
      }, function(response) {
            // handle error
          });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .list-box {
    overflow: hidden;
  }
  .list-box li {
    padding: 20px 10px;
    display: flex;
    border-bottom: 1px solid #f4f4f4;
  }
  .list-box li .pic {
    width: 1.2rem;
    height: 1.2rem;
    border-radius: 50%;
    overflow: hidden;
  }
  .item-flex {
    -webkit-box-flex: 0;
    -ms-flex-positive: 0;
    flex-grow: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
  }
  .list-box li .pic > img {
    display: block;
    width: 100%;
  }
  .list-box li h2 {
    height: 50%;
    line-height: 1.5;
    color: #622A1D;
    font-size: .37rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .list-box li .box {
    width: 85%;
    height: 1.2rem;
    padding-left: 10px;
  }
  .list-box li .detail {
    height: 50%;
    display: flex;
    color: #aaa;
    padding: 10px 10px 10px 0;
    align-items: center;
    justify-content: space-between;
    font-size: .32rem;
  }
  .list-box li .detail .col .iconfont {
    text-align: center;
    font-size: .3rem;
  }
  .loading {
    font-size: .4rem;
    height: 50px;
    text-align: center;
    line-height: 50px;
  }
  .loading:active {
    background: #ddd;
  }
</style>



