<template>
  <div id="app">
    <div class="wrap" :class="{sliderShow: isShowSlider}">
      <Main></Main>
      <Detail></Detail>
      <Headers></Headers>
      <Slider></Slider>
    </div>
  </div>
</template>

<script>
  import viewport from './app/viewport'
  import Main from './components/Main'
  import Slider from './components/Slider'
  import Header from './components/Header'
  import Detail from './components/Detail'


  export default {
    data () {
      return {
        isShowSlider: false
      }
    },
    components: {
      'Main': Main,
      'Slider': Slider,
      'Headers': Header,
      'Detail': Detail,
    },
     events: {
        'show-meun': function(msg) {
          this.isShowSlider = msg
        }
    },
  }
</script>

<style>
  html,body,div,h1,h2,h3,h4,ul,li,p {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }

@font-face {
  font-family: 'iconfont';
  src: url('//at.alicdn.com/t/font_1472109320_852638.eot'); /* IE9*/
  src: url('//at.alicdn.com/t/font_1472109320_852638.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('//at.alicdn.com/t/font_1472109320_852638.woff') format('woff'), /* chrome、firefox */
  url('//at.alicdn.com/t/font_1472109320_852638.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
