}
                    
                    
  .iconfont{
    font-family:"iconfont";
     font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: .2px;
    -moz-osx-font-smoothing: grayscale;
}

  html {
    height: 100%;
  }

  body {
    height: 100%;
  }

  #app {
    color: #2c3e50;
    font-size: .25rem;
    font-family: Source Sans Pro, Helvetica, sans-serif;
  }

  .wrap {
    position: relative;
    overflow: hidden;
  }

  li {
    cursor: pointer;
  }

  .isFinished {
    color: #f40;
  }

  #app a {
    color: #622A1D;
    text-decoration: none;
  }

  .logo {
    width: 100px;
    height: 100px
  }

  .tran-top {
