<template>
  <div class="header">
    <a class="iconfont all tran-top btn" @click="openMeun()">&#xe608;</a>
    <h1 @click="onClickMe()">{{ msg }}</h1>
    <a class="iconfont go tran-top btn">&#xe600;</a>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg: 'All',
      isShowSlider: false
    }
  },

  props: ['headername'],
  methods: {
    onClickMe: function() {
      // console.log(this.headername)
      this.$emit('tell-you', this)
    },
    openMeun: function() {
      this.isShowSlider = !this.isShowSlider
      this.$dispatch("show-meun", this.isShowSlider);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 48px;
    line-height: 48px;
    text-align: center;
    background-color: #ddd;
    background: linear-gradient(160deg, #f4f4f4, #ddd);
    box-shadow: 0 0 2px #ddd;
    transition: transform ease-in .15s;
  }
  .sliderShow .header {
    transform: translateX(200px);
  }
  .header .all {
    left: 5%;
    font-size: .6rem;
  }
   .header .go {
    right: 5%;
    font-size: .6rem;
  }
  h1 {
    font-size: .5rem;
    color: #666;
  }
</style>



