<template>
  <div class="slider-meun">
    <div class="bg"></div>
    <div class="icon">
      <i class="iconfont logo">&#xe60c;</i>
    </div>
    <div class="meun">
      <ul>
        <li><i class="iconfont">&#xe607;</i>全部</li>
        <li><i class="iconfont">&#xe609;</i>精华</li>
        <li><i class="iconfont">&#xe60d;</i>分享</li>
        <li><i class="iconfont">&#xe601;</i>回答</li>
        <li><i class="iconfont">&#xe60b;</i>招聘</li>
        <li><i class="iconfont">&#xe605;</i>关于</li>
      </ul>
    </div>
  </div>
  <div class="slider-modal"></div>
</template>

<script>
export default {
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      msg: 'Hello Worldsss!',
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.slider-meun .meun {
  position: absolute;
  top: 80px;
  left: 10%;
  font-size: .4rem;
  width: 80%;
  color: #ddd;
}
.slider-meun .meun li {
  height: 38px;
  line-height: 38px;
  list-style: none;
  padding-left: 20px;
}
.slider-meun .meun li:nth-child(5) {
  border-bottom: 1px solid #aaa;
}
.slider-meun .meun li .iconfont {
  margin-right: 8px;
  color: #f4f4f4;
}
.slider-meun {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    width: 200px;
    background-color: #f4f4f4;
    overflow: hidden;
    transform: translateX(-200px);
    transition: transform ease-in .15s;
}
.slider-modal {
   position: fixed;
    top: 48px;
    left: 200px;
    bottom: 0;
    right: 0;
    opacity: 0;
    background: rgba(0,0,0,.1);
    transition: opacity .2s;
    transform: translateX(100%);
}
.slider-meun .bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: url('../assets/nature.jpg');
    -webkit-filter:blur(50px);
}
.slider-meun .icon {
  position: absolute;
  top: 0;
  left: 10%;
  width: 80%;
  padding: 10px;
  border-bottom: 1px solid #fff;
}
 .sliderShow .slider-meun {
    opacity: 1;
    transform: translateX(0);
 }
 .sliderShow .slider-modal {
    opacity: 1;
    transform: translateX(0);
 }
 .logo {
  color: #622A1D;
  font-size: 40px;
 }
</style>



